.btn {
    @apply cursor-pointer text-sm font-semibold py-2 px-6 border border-gray-300 bg-gray-100 text-gray-800 rounded transition duration-200 shadow-sm inline-block relative focus:ring focus:ring-indigo-200 focus:ring-opacity-50 hover:bg-gray-200;

    &-sm {
        @apply text-xs py-1 px-3;
    }

    &-primary {
        @apply border-transparent bg-blue-700 text-white hover:bg-blue-800;
    }

    &[disabled] {
        @apply opacity-50 cursor-not-allowed;
    }
}
